@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

nav {
  height: 10vh;
  background: #222;

  .nav-center {
    max-width: 1280px;
    margin: 0 auto;
    width: 90vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    height: 10vh;
  }

  .logo {
    width: 5rem;
  }
}

.nav-links {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: nowrap;
  gap: 0.5rem;
  list-style: none;
  flex-grow: 1;
  max-width: 50%;
}

.nav-link {
  text-decoration: none;
  color: whitesmoke;
  position: relative;
  padding-bottom: 0.25rem;

  @media (min-width: 792px) {
    font-size: 1.25rem;
  }

  &::after {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    bottom: 0;
    left: 0;
    background: white;
    transition: all 0.3s linear;
  }

  &:hover::after {
    width: 100%;
  }
}

.hero {
  background-image: linear-gradient(to right, #ed6ea0 0%, #ec8c69 100%);
  background-image: linear-gradient(
    315deg,
    #a445b2 0%,
    #d41872 52%,
    #ff0066 100%
  );
}
.about {
  background-image: linear-gradient(to left, #243949 0%, #517fa4 100%);
}
.contact {
  background-image: linear-gradient(
    to right,
    #c1c161 0%,
    #c1c161 0%,
    #d4d4b1 100%
  );
}

.hero,
.about,
.contact {
  height: 90vh;
  position: absolute;
  width: 100%;
}

.banner {
  max-width: 1280px;
  margin: 0 auto;
  width: 90vw;
  display: grid;
  place-items: center;
  height: 100%;
  text-align: center;
  gap: 3rem;

  .banner-img {
    align-self: center;
    max-width: 30rem;
    img {
      width: 100%;
      object-fit: contain;
    }
  }

  h1 {
    font-size: 2.5rem;
    margin: 2rem 0;
    color: white;
    text-shadow: 3px 3px 3px #222;
  }

  p {
    color: white;
  }

  @media (min-width: 992px) {
    grid-template-columns: 1fr auto;
    text-align: left;
    gap: 6rem;

    h1 {
      font-size: 4rem;
    }

    p {
      font-size: 1.25rem;
      max-width: 40rem;
    }
  }
}
